$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;

.fb-video-options-setting {
    .components-base-control {
        margin-bottom: 0;
    }
}
.components-form-toggle {
    .components-form-toggle__input:focus + .components-form-toggle__track {
        outline: none;
        box-shadow: none;
    }
}

.fb-parent-wrapper {
    filter: unset;
    .fb-advanced-video-wrapper {
    display: block;
    overflow: hidden;
    position: relative;
    transition: .5s;
    transition: border 0.5s, border-radius 0.5s, box-shadow 0.5s;
        .fb-player-wrapper {
            padding-top: 56.25%;
            position: relative;
            transition: transform 0.5s, border 0.5s, border-radius 0.5s, box-shadow 0.5s;
            .fb-player-option {
                .fb-react-player {
                    left: 0;
                    position: absolute;
                    top: 0;
                    iframe {
                        box-sizing: border-box;
                    }

                    .react-player__shadow {
                        display: none !important;
                    }
                }
            }
        }
    }
}

.fb-advanced-video-main-wrapper {
    width: 100%;
    &:focus-visible {
        outline: none;
    }
    .fb-parent-wrapper {
        &:focus-visible {
            outline: none;
        }
        .fb-advanced-video-wrapper {
            &:focus-visible {
                outline: none;
            }
            .fb-player-wrapper {
                &:focus-visible {
                    outline: none;
                }
                .fb-player-option {
                    &:focus-visible {
                        outline: none;
                    }
                    .fb-react-player {
                        &:focus-visible {
                            outline: none;
                        }
                        div{
                            &:focus-visible {
                                outline: none;
                            }
                        }
                    }
                }
            }
        }
    }
}